#calc-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; 

  background: linear-gradient(60deg, green, blue) ;
  /* don't know why, but it works like that */
} 

#calc {
  display: grid;
  grid-template-columns: repeat(4, 70px);
  grid-template-rows: auto;
  background-color: rgb(172, 255, 47);
}

/* Button and result */

.calc__button {
  height: 70px;
  width: 70px;

  background-color: rgb(255, 255, 255);
  color: black;
  display: flex;

  justify-content: center;
  align-items: center;
  
  cursor: pointer;
  font-size: 35px;
}

.calc__button:hover {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.calc__button#equals {
width: 280px;
}

#display {
  grid-area: 1/1/2/5;
  width: 100%;
  height: 90px;
  font-size: 40px;
  overflow: auto;
}

#clock-holder {
  display: grid;

  width: 100%;
  height: 95vh;

  grid-template-columns: 1.5fr 2fr 1.5fr;
  grid-gap: 10px;
  gap: 10px;

  justify-items: center;
  align-items: center;

  background-color: rgb(37, 37, 37);
}


.time-block {
  display: flex;

  flex-wrap: nowrap;
  flex-direction: column;
  font-size: 25px;

  height: 35%;
  width: 100%;

  justify-content: center;
  align-items: center;

  opacity: 1;

  transition: all 400ms ease-in;

  background-color: rgb(221, 221, 221);
  border: 1px solid rgb(221, 221, 221);
  border-radius: 27px;
}

.time-block#main-timer {
  font-size: 35px;
  height: 55%;
}

#clock-holder__navbar {
  position: fixed;
  top: 0;
  left: 0;
  
  width: 100%;
  height: 7vh;

  background-color: rgb(26, 27, 32);
  color: white;

  text-align: center;
}

#clock-holder__navbar p {
  margin-top: 1vh;

  font-size: 25px;
}

#clock-holder button {
  margin: 15px;
  padding: 10px;
  font-size: 15px;

  background-color: rgb(160, 160, 160);
  color: rgb(0, 0, 0);

  transition: all 300ms ease-out;

  outline-style: none;
}

#clock-holder button:not(.big-button) {
  border-radius: 10px;
  color: black;
  font-weight: 450;
}

#clock-holder button:hover {
  cursor: pointer;

  background-color: rgb(26, 27, 32);
  color: rgb(221, 221, 221);
}

#clock-holder button:focus {
  background-color: rgb(87, 168, 87);
}

.big-button {
  padding: 20px;
  font-size: 20px;
}

.disabled {
  position: relative;
  opacity: 0;
  visibility: hidden;
  top: 35%;
}

.enabled {
  position: relative;
  opacity: 1;
  visibility: visible;
  top: 0%;
}
